import React from "react";

export const NumberedList: React.FC<{
    data: Array<string>;
}> = ({ data }) => {
    return (
        <ul className="mt-8 mb-10 mobile:mb-0 mobile:mt-6">
            {data.map((text, i) => (
                <li key={text} className="flex mt-6 mobile:mt-4">
                    <div>
                        <span className="w-9 h-9 min-w-9 min-h-9 block rounded-full border-[#1E282D] border text-center leading-9 text-xl mr-4">
                            {i + 1}
                        </span>
                    </div>
                    <span className="text-2xl mobile:text-lg font-medium">
                        {text}
                    </span>
                </li>
            ))}
        </ul>
    );
};
